*, :before, :after {
  box-sizing: border-box;
}

html {
  tab-size: 4;
  -webkit-text-size-adjust: 100%;
  line-height: 1.15;
}

body {
  margin: 0;
  font-family: system-ui, AppleSystem, BlinkMacSystemFont, Segoe UI, Roboto, Noto Sans, Ubuntu, Cantarell, Helvetica Neue, AppleSystem, BlinkMacSystemFont, Segoe UI, Roboto, Noto Sans, Ubuntu, Cantarell, Helvetica Neue, -apple-system, Segoe UI, Roboto, Helvetica, Arial, sans-serif, Apple Color Emoji, Segoe UI Emoji;
}

hr {
  height: 0;
  color: inherit;
}

abbr[title] {
  -webkit-text-decoration: underline dotted;
  text-decoration: underline dotted;
}

b, strong {
  font-weight: bolder;
}

code, kbd, samp, pre {
  font-family: ui-monospace, SFMono-Regular, Consolas, Liberation Mono, Menlo, monospace;
  font-size: 1em;
}

small {
  font-size: 80%;
}

sub, sup {
  vertical-align: baseline;
  font-size: 75%;
  line-height: 0;
  position: relative;
}

sub {
  bottom: -.25em;
}

sup {
  top: -.5em;
}

table {
  text-indent: 0;
  border-color: inherit;
}

button, input, optgroup, select, textarea {
  margin: 0;
  font-family: inherit;
  font-size: 100%;
  line-height: 1.15;
}

button, select {
  text-transform: none;
}

button, [type="button"], [type="reset"], [type="submit"] {
  -webkit-appearance: button;
}

::-moz-focus-inner {
  border-style: none;
  padding: 0;
}

:-moz-focusring {
  outline: 1px dotted ButtonText;
}

:-moz-ui-invalid {
  box-shadow: none;
}

legend {
  padding: 0;
}

progress {
  vertical-align: baseline;
}

::-webkit-inner-spin-button, ::-webkit-outer-spin-button {
  height: auto;
}

[type="search"] {
  -webkit-appearance: textfield;
  outline-offset: -2px;
}

::-webkit-search-decoration {
  -webkit-appearance: none;
}

::-webkit-file-upload-button {
  -webkit-appearance: button;
  font: inherit;
}

summary {
  display: list-item;
}

:root {
  --wight-text-color: #fff;
  --black-text-color: #000;
  --yellow-text-color: #f3c653;
  --brown-text-color: #c79002;
  --grey-text-color: #5f5f5f;
  --second-grey-color: #bababa;
  --second-yellow-color: rgba(243, 198, 83, .7);
  --hero-address-color: #e5e5e5;
  --contacts-color: #828282;
  --mimino-first-color: #828282;
  --mimino-second-color: #494848;
  --main-bg-color: #fffdee;
  --secondary-bg-color: #f0ecd6;
  --section-title-color: #b4b4b4;
  --hotel-text-color: #7a7a7a;
  --modal-text-color: gray;
  --footer-bg-color: #0e0c07;
  --main-box-shadow-color: 5px 5px 70px 17px rgba(0, 0, 0, .25);
  --button-bg-color--black: #000;
  --button-bg-color--gray: rgba(0, 0, 0, .4);
  --button-border-accent-color: #f3c653;
  --button-accent-color: #f3c653;
  --cubic-primary: cubic-bezier(.4, 0, .2, 1);
  --backdrop-color: rgba(0, 0, 0, .2);
  --ligth-v-line-color: rgba(186, 186, 186, .4);
  --dark-v-line-color: rgba(122, 122, 122, .4);
  --scrollbar-width: 15;
  --coef: 1;
}

