.button {
  cursor: pointer;
  font-family: inherit;
}

.button__primary {
  letter-spacing: .02em;
  color: #f3c653;
  transition: border-color 250ms var(--cubic-primary), background-color 250ms var(--cubic-primary);
  border-radius: 25px;
  font-size: 16px;
  font-weight: 700;
  line-height: 1.25;
}

.button__primary--transparent {
  background-color: var(--button-bg-color--transparent);
  border: 2px solid #fff;
}

.button__primary--transparent:hover, .button__primary--transparent:focus {
  background-color: #fff;
  border: 2px solid #fff;
}

.button__primary--black {
  background-color: var(--button-bg-color--black);
  border: 2px solid #fff;
}

.button__primary--black:hover, .button__primary--black:focus {
  border: 2px solid var(--button-border-accent-color);
  background-color: #fff;
}

.button__secondary {
  background-color: var(--button-bg-color--black);
  border: 1px solid var(--button-border-accent-color);
  color: var(--wight-text-color);
  text-transform: uppercase;
  transition: border 250ms var(--cubic-primary), background-color 250ms var(--cubic-primary), color 250ms var(--cubic-primary);
  border-radius: 25px;
  font-size: 16px;
  font-weight: 500;
  line-height: 1.2;
}

.button__secondary:hover, .button__secondary:focus {
  background-color: var(--button-accent-color);
  border: 1px solid var(--button-border-accent-color);
  color: var(--black-text-color);
  text-transform: uppercase;
  transition: border 250ms var(--cubic-primary), background-color 250ms var(--cubic-primary), color 250ms var(--cubic-primary);
  border-radius: 25px;
  font-size: 16px;
  font-weight: 500;
  line-height: 1.2;
}

.button__modal {
  background-color: var(--button-bg-color--black);
  border: 1px solid var(--button-border-accent-color);
  color: var(--wight-text-color);
  text-transform: uppercase;
  transition: border 250ms var(--cubic-primary), background-color 250ms var(--cubic-primary), color 250ms var(--cubic-primary);
  border-radius: 2px;
  font-size: 20px;
  font-weight: 500;
  line-height: 1.2;
}

.button__modal:hover, .button__modal:focus {
  background-color: var(--button-accent-color);
  border: 1px solid var(--button-border-accent-color);
  color: var(--black-text-color);
  text-transform: uppercase;
  transition: border 250ms var(--cubic-primary), background-color 250ms var(--cubic-primary), color 250ms var(--cubic-primary);
  border-radius: 2px;
  font-size: 20px;
  font-weight: 500;
  line-height: 1.2;
}

.button__mobile {
  text-transform: uppercase;
  transition: border 250ms var(--cubic-primary), background-color 250ms var(--cubic-primary), color 250ms var(--cubic-primary);
  font-size: 20px;
  font-weight: 500;
  line-height: 1.2;
