.backdrop {
  width: 100vw;
  height: 100vh;
  background-color: var(--backdrop-color);
  visibility: visible;
  opacity: 1;
  transition: visibility var(--transition-duration) var(--cubic-primary), opacity var(--transition-duration) var(--cubic-primary);
  position: fixed;
  top: 0;
  left: 0;
  overflow-y: scroll;
}

.backdrop--is-hidden, .backdrop--not-hidden {
  visibility: hidden;
  opacity: 0;
  pointer-events: none;
}

.backdrop__modal {
  position: absolute;
  top: 50%;
  left: 50%;
}

.modal {
  width: 100%;
